import { observable } from 'mobx';

const aticleVokeData = {
	title:'',
	avater:'./images/small-pic.png',
	author:'',
	publistTime:'30',
	content:'NoteBookCheckKaby LadyIntelIntel i7-7500U3.0GHzIntel HD Graphics 620',
	picture:['./images/1.jpg'],
	youyong:150,
	share:120,
	comments:[{
		avater:'./images/small-pic.png',
		time:'5',
		username:'',
		text:'Not'
	},
	{
		avater:'./images/small-pic.png',
		time:'5',
		username:'',
		text:'Note',

	},
	{
		avater:'./images/small-pic.png',
		time:'5',
		username:'',
		text:'Note',
		ref:1
	}]
}

class Article {
	@observable article = {};
	getArticle(id) {
		this.article = aticleVokeData;
	}
	constructor(id) {
		this.getArticle(id);
	}
}


export default Article;


